.a-link--partially-styled {
  border-bottom-width: 0;
}
.a-link--partially-styled .a-link__plain {
  color: var(--black);
  border-bottom: 1px dotted transparent;
}
.a-link--partially-styled .a-link__underlined {
  border-bottom: 1px dotted var(--pacific);
}
.a-link--partially-styled:visited .a-link__underlined {
  border-bottom: 1px dotted var(--teal);
}
.a-link--partially-styled:hover span, .a-link--partially-styled:focus span {
  border-bottom: 1px solid var(--pacific-dark) !important;
}
.a-link--partially-styled:active span {
  border-bottom: 1px solid var(--navy) !important;
}